.JoinButtonBox {
  display: inline-block;
  border-radius: 28px !important;
  width: 160px;
  height: 56px;
  border-radius: 7px;
  background: #ffdda9;
  user-select: none;
  text-align: center;
  line-height: 60px;

  font-family: "DM Sans";
  font-weight: 700;
  line-height: 26px !important;
  text-align: center;
  letter-spacing: -0.36px;
  color: #12022f;
}

@media screen and (max-width: 768px) {
  .JoinButtonBox {
    width: 200px;
  }
}
