.headerContainer {
  background: linear-gradient(to right, #1d0458, #510b76);
}

.headerContent {
  max-width: 1240px;
  margin: 0 auto;
}

.headerFlex {
  width: "90%";
  align-items: center;
  justify-content: space-between;
  color: white;
  height: 105px;
  gap: 50px;
  font-family: "Rumiko Clear Demo", sans-serif;
  font-size: 16px;
  color: #ffffff;

  margin-bottom: -1px;
}

.headerLogoWrap {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  gap: 10px;
}

.headerLogo {
  display: inline-block;
  width: 26px;
  height: 26px;
  background-size: cover;
  background: url("../../assets/image/logo.png");
  background-repeat: no-repeat;
}

.headerLogoText {
  display: inline-block;

  font-family: "DM Sans";
  font-weight: 700;
  font-size: 20px;
  line-height: 26px;
}
.headerLinkContainer {
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  gap: 20px;
}

.headerLink {
  /* margin-right: 30px; */
  font-family: "DM Sans";
  font-style: normal;
  font-weight: 700;
  font-size: 16px;
  line-height: 26px;
}

.headerHamburger {
  display: none;
}

@media screen and (max-width: 768px) {
  .headerLinkContainer {
    display: none;
  }
  .headerFlex {
    justify-content: space-around;
    gap: none;
  }
  .headerHamburger {
    display: inline-block;
    width: 40px;
    height: 40px;
    transition: all 1s ease;
  }
  .headerLaunchBtn {
    display: none;
  }
  .hambergerLink {
    font-family: "DM Sans";
    font-weight: 700;
    font-size: 16px;
    line-height: 26px;
    letter-spacing: -0.355556px;
    color: #000000;
    margin: 30px 0;
  }
  .hambergerHide {
    right: -400px !important;
  }
}
